import pagesJson from '@/pages.json'
import config from '@/uni_modules/uni-id-pages/config.js'

//  uniCloud
function getUniIdCo() {
	if (!uniCloud || !uniCloud.importObject) {
		console.warn('[uni-id-pages] uniCloud ')
		return null
	}
	return uniCloud.importObject("uni-id-co")
}

function getDatabase() {
	if (!uniCloud || !uniCloud.database) {
		console.warn('[uni-id-pages] uniCloud ')
		return null
	}
	return uniCloud.database()
}

function getUsersTable() {
	const db = getDatabase()
	if (!db) return null
	return db.collection('uni-id-users')
}

const uniIdCo = getUniIdCo()

let hostUserInfo = uni.getStorageSync('uni-id-pages-userInfo')||{}

const data = {
	userInfo: hostUserInfo,
	hasLogin: Object.keys(hostUserInfo).length != 0
}

//  mutations, 
export const mutations = {
	// data(),
	async updateUserInfo(data = false) {
		if (data) {
			const usersTable = getUsersTable()
			if (!usersTable) {
				console.warn('[uni-id-pages] uniCloud ')
				return
			}
			usersTable.where('_id==$env.uid').update(data).then(e => {
				// console.log(e);
				if (e.result.updated) {
					uni.showToast({
						title: "",
						icon: 'none',
						duration: 3000
					});
					this.setUserInfo(data)
				} else {
					uni.showToast({
						title: "",
						icon: 'none',
						duration: 3000
					});
				}
			})

		} else {
      // _idstore.userInfo_id
			let _id = null
			try {
				const userInfo = uniCloud.getCurrentUserInfo()
				_id = userInfo ? userInfo.uid : null
			} catch (e) {
				console.warn('[uni-id-pages] :', e)
			}
			if (_id) {
				this.setUserInfo({_id},{cover:true})
			}
      // store.userInfo
			const uniIdCo = getUniIdCo() || uniCloud.importObject("uni-id-co", {
				customUI: true
			})
			const usersTable = getUsersTable()
			if (!usersTable) {
				console.warn('[uni-id-pages] uniCloud ')
				return
			}
			try {
				let res = await usersTable.where("'_id' == $cloudEnv_uid")
					.field('mobile,nickname,username,email,avatar_file')
					.get()

				const realNameRes = await uniIdCo.getRealNameInfo()

				// console.log('fromDbData',res.result.data);
				this.setUserInfo({
					...res.result.data[0],
					realNameAuth: realNameRes
				})
		} catch (e) {
			this.setUserInfo({},{cover:true})
			console.error(e.message, e.errCode);
			
			// 
			if (e.errCode === 'uni-id-account-not-exists' || e.message?.includes('')) {
				console.warn('[uni-id-pages] ')
			}
		}
		}
	},
	setUserInfo(data, {cover}={cover:false}) {
		// console.log('set-userInfo', data);
		let userInfo = cover?data:Object.assign(store.userInfo,data)
		store.userInfo = Object.assign({},userInfo)
		store.hasLogin = Object.keys(store.userInfo).length != 0
		// console.log('store.userInfo', store.userInfo);
		uni.setStorageSync('uni-id-pages-userInfo', store.userInfo)
		return data
	},
	async logout() {
		// 1. 	2.
		try {
			const userInfo = uniCloud.getCurrentUserInfo()
			if(userInfo && userInfo.tokenExpired > Date.now()){
				const currentUniIdCo = getUniIdCo() || uniIdCo
				if (currentUniIdCo) {
					try{
						await currentUniIdCo.logout()
					} catch(e){
						console.error(e);
					}
				}
			}
			
			//  
			if (userInfo && userInfo.uid) {
				uni.removeStorageSync('admin_auto_redirected_' + userInfo.uid);
			}
		} catch (e) {
			console.warn('[uni-id-pages] :', e)
		}
		uni.removeStorageSync('uni_id_token');
		uni.setStorageSync('uni_id_token_expired', 0)
    this.setUserInfo({},{cover:true})
    uni.$emit('uni-id-pages-logout')
		uni.redirectTo({
			url: `/${pagesJson.uniIdRouter && pagesJson.uniIdRouter.loginPage ? pagesJson.uniIdRouter.loginPage: 'uni_modules/uni-id-pages/pages/login/login-withpwd'}`,
		});
	},
	loginBack (e = {}) {
		let {uniIdRedirectUrl = ''} = e
		
		// 
		try {
			const token = uni.getStorageSync('uni_id_token') || '';
			if (token) {
				const tokenArr = token.split('.');
				if (tokenArr.length === 3) {
					try {
						const payload = JSON.parse(decodeURIComponent(escape(atob(tokenArr[1]))));
						const role = payload.role || [];
						const isAdmin = Array.isArray(role) ? role.includes('admin') : role === 'admin';
						
						if (isAdmin) {
							// 
							return uni.reLaunch({
								url: '/pages/admin/dashboard/dashboard'
							})
						}
					} catch (e) {
						console.warn('[store]  token :', e);
					}
				}
			}
		} catch (error) {
			console.warn('[store] :', error);
		}
		
		// 
		if (!uniIdRedirectUrl) {
			uniIdRedirectUrl = uni.getStorageSync('redirectUrl') || ''
			if (uniIdRedirectUrl) {
				console.log('[store] :', uniIdRedirectUrl)
				// 
				uni.removeStorageSync('redirectUrl')
			}
		}
		
		let delta = 0; //
		let pages = getCurrentPages();
		// console.log(pages);
		pages.forEach((page, index) => {
			if (pages[pages.length - index - 1].route.split('/')[3] == 'login') {
				delta++
			}
		})
		// console.log(':', delta);
		if (uniIdRedirectUrl) {
			return uni.redirectTo({
				url: uniIdRedirectUrl,
				fail: (err1) => {
					uni.switchTab({
						url:uniIdRedirectUrl,
						fail: (err2) => {
							console.log(err1,err2)
						}
					})
				}
			})
		}







		if (delta) {
			const page = pagesJson.pages[0]
			return uni.reLaunch({
				url: `/${page.path}`
			})
		}

		uni.navigateBack({
			delta
		})
	},
	loginSuccess(e = {}){
		const {
			showToast = true, toastText = '', autoBack = true, uniIdRedirectUrl = '', passwordConfirmed
		} = e
		// console.log({toastText,autoBack});
		if (showToast) {
			uni.showToast({
				title: toastText,
				icon: 'none',
				duration: 3000
			});
		}
    // 
		this.updateUserInfo()

		uni.$emit('uni-id-pages-login-success')

		if (config.setPasswordAfterLogin && !passwordConfirmed) {
			return uni.redirectTo({
				url: uniIdRedirectUrl ? `/uni_modules/uni-id-pages/pages/userinfo/set-pwd/set-pwd?uniIdRedirectUrl=${uniIdRedirectUrl}&loginType=${e.loginType}`: `/uni_modules/uni-id-pages/pages/userinfo/set-pwd/set-pwd?loginType=${e.loginType}`,
				fail: (err) => {
					console.log(err)
				}
			})
		}

		if (autoBack) {
			this.loginBack({uniIdRedirectUrl})
		}
	}

}


import Vue from 'vue'
// Vue.observable
export const store = Vue.observable(data)









